:root {
  --color-active: #00acdc;
}

html {
  text-align: left;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
}

body {
  background-color: #c5c5c5;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.logo {
  margin-left: 50px;
}

.nav {
  align-items: center;
  margin-left: auto;
  margin-right: 50px;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin: 0 10px;
  display: flex;
  position: relative;
}

.style {
  color: #000;
  padding: 23px 0;
  text-decoration: none;
}

.nav__link:last-child {
  margin-right: 0;
}

.nav__link:first-child {
  margin-left: 0;
}

.style:hover, .is-active {
  color: var(--color-active);
}

.is-active:after {
  content: "";
  box-sizing: border-box;
  border: 1px solid var(--color-active);
  background-color: var(--color-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
